@import url('https://fonts.googleapis.com/css?family=Roboto:300');

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

.main {
    height: 100vh;
}

h1, h2 {
    text-align: center;
    font-weight: 300;
    font-size: 3rem;
}

h1 {
    padding: 20px;
}

h2 {
    font-size: 2rem;
    margin-top: 0;
}

.img-container {
    margin: 0 auto;
    height: 400px;
}

.mui-panel {
    padding: 0;
}

img {
    max-height: 350px;
    margin: 30px auto;
}

.rnd {
    width: 200px;
    margin: 0 auto;
}

.wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 80%;
    margin: 0 auto;
}

fieldset {
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    padding: 0;
    border: 0;
}

form {
    display: -ms-flexbox;
    display: flex;
    margin: 0 auto;
    width: 99%;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

button {
    max-width: 300px;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

@media all and (max-width: 1000px) {
    .img-container {
        height: auto;
        max-width: 80%;
    }

    img {
        max-height: auto;
        max-width: 100%;
    }

    h1 {
        padding: 0;
    }

}


